<template>
  <el-container>
    <el-header>
      <div class="title">视频处理</div>
      <div class="user">
        <el-dropdown>
          <span class="el-dropdown-link">
            <div class="username">
              {{ user.username }}
            </div>
            <i class="iconfont icon-xia" style="font-size: 12px; margin-left: 8px"></i>
          </span>
          <template #dropdown>
            <el-dropdown-menu>
              <el-dropdown-item @click="handleLogout">
                <div style="padding: 5px 16px">退出登陆</div>
              </el-dropdown-item>
            </el-dropdown-menu>
          </template>
        </el-dropdown>
      </div>
    </el-header>
    <el-container>
      <el-aside width="200px">
        <el-menu :default-active="$route.path" router>
          <div
            v-for="(item, index) in menus.filter(item => (item.ifShow ? item.ifShow() : true))"
            :key="index"
          >
            <el-menu-item :index="item.path">
              <span>{{ item.name }}</span>
            </el-menu-item>
          </div>
        </el-menu>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script setup>
import { useRouter } from 'vue-router'

import { getUser, removeUser } from '@/utils/user'

// 用户信息
const user = getUser()

// 路由对象
const router = useRouter()
// 退出登陆
const handleLogout = () => {
  // 清除用户信息
  removeUser()
  // 跳转到登陆页面
  router.push('/login')
}

// 菜单
const menus = [
  {
    path: '/video',
    name: '视频处理'
  },
  {
    path: '/file',
    name: '素材管理'
  },
  {
    path: '/user',
    name: '用户管理',
    ifShow: () => user.isAdmin
  }
]
</script>

